import React, { Component } from 'react';
import RichTextEditor from 'react-rte';

export default class SendNoteAll extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active_choose_user_to_send: false,
      value: RichTextEditor.createEmptyValue()
    };
  }

  onChange = value => {
    this.setState({ value });
    if (this.props.onChange) {
      this.props.onChange(value.toString('html'));
    }
  };

  toggle_choose_user = () => {
    this.setState({
      active_choose_user_to_send: !this.state.active_choose_user_to_send
    });
  };

  render() {
    return (
      <div className="send-note-all">
        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix">
              <span>Gi mail cho tt c</span>
            </div>
            <div className="block-content-body">
              <form action="">
                <div className="mail-title">
                  <label className="d-block p-2">
                    Tiu <span className="text-danger"> * </span>
                    <span className="text-required text-danger text-hide">
                      This field cannot be empty
                    </span>
                  </label>
                  <input type="text" className="text-dark p-2 w-100" />
                </div>
                <div className="mail-content">
                  <label className="d-block p-2">
                    Ni dung<span className="text-danger"> * </span>
                    <span className="text-required text-danger text-hide">
                      This field cannot be empty
                    </span>
                  </label>
                  <RichTextEditor value={this.state.value} onChange={this.onChange} />
                </div>
                <div className="mail-file">
                  <input type="file" className="form-control-file" name="choose-file" />
                </div>
                <div className="mini-block-content collapse target-content-user">
                  <table className="table table-hover">
                    <thead className="thead-dark">
                      <tr className="">
                        <th className="">Id</th>
                        <th className="">H v tn</th>
                        <th className="">Chi tit</th>
                        <th className="" />
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Id con g</td>
                        <td>Con g l cha con zt</td>
                        <td>Con g c 2 ci chn</td>
                        <td>
                          <input type="checkbox" />
                        </td>
                      </tr>
                      <tr>
                        <td>Con vt l m con g</td>
                        <td>id con vt</td>
                        <td>Con vt c 2 ci cnh</td>
                        <td>
                          <input type="checkbox" />
                        </td>
                      </tr>
                      <tr>
                        <td>Con g p con vt</td>
                        <td>id con chicken</td>
                        <td>2 con 4 ci chn</td>
                        <td>
                          <input type="checkbox" />
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div className="mail-button">
                  <button className="p-2 mr-2 mt-2 border bg-hover-gainsboro" type="submit">
                    Gi
                  </button>
                  <button className="p-2 mr-2 mt-2 border bg-hover-gainsboro">Xa</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



